<template>
	<br />
	<span>Choose a Username: </span>
	<br />
	<input v-model="userName" v-on:keyup.enter="trySave()"/>
	<br />
	<button class="btn" @click="trySave()">Save</button>

	<div v-if="!$store.state.userNameAvailable" id="warning">
		<br />
		This username is unavailable
	</div>

    <br /><br />

</template>

<script>
export default {
	data() {
		return {
			userName: "",
			userNameAvailable: true,
		};
	},
	methods: {
		trySave() {
            this.$store.dispatch("checkUserNameAvailabilityAndTrySave",this.userName);
        },
	},
};
</script>

<style scoped>
span {
	font-size: 21px;
	text-align: left;
}

input {
	padding: 5px 10px;
	font-size: 21px;
	margin: 10px;
}
#warning {
	color: #dc3545;
}
</style>