<template>
	<div class="notes" v-if="$store.state.userIsAuthenticated && this.$store.state.userEmail">

        <div class="user">
            <div class="user-data" v-if="$store.state.userData">
                <h2>Hello {{$store.state.userData.username}}</h2>
            </div>
            
            <div class="create-user" v-if="$store.state.userToBeCreated">
                <CreateUser></CreateUser>
            </div>
        </div>

        <NotesList></NotesList>

		<div class="new-note">
			<input
				class="new-note-input"
				v-model="new_note"
				@click="deselect()"
				v-on:keyup.enter="createNote()"
			/>
		</div>
	</div>
</template>

<script>
import NotesList from "@/components/NotesList.vue";
import CreateUser from "./CreateUser.vue";
export default {
    name: "Notes",
    components:{
    NotesList,
    CreateUser
},
	data() {
		return {
			new_note: "",
		};
	},
	methods: {
		deselect() {
            // this.selected_note = null;
            // selected note in state
		},
		createNote() {
			if (this.new_note.length > 0) {
				this.$store.commit("createNote", {
					note: this.new_note,
					callback: (note) => {},
				});
				this.new_note = "";
			}
		},
	},
    beforeCreate(){
        if (this.$store.state.userIsAuthenticated) {
            if(!this.$store.state.userEmail){
                this.$store.dispatch("auth0getUserEmail");
            }
		}
    }
};
</script>

<style>


.new-note-input {
	float: left;

	font: inherit;
	font-size: inherit;
	color: inherit;

	margin-left: 0.5em;
	padding: 0.3em;
	padding-left: 0.5em;

	width: calc(100% - 1em);
	box-sizing: border-box;

	border: solid;
	border-width: 1px;
	border-radius: 4px;
	outline: none;
}

</style>