<template>
  
  <div class="notes-list"
			v-if="$store.state.all.length > 0"
			v-for="(note, i) in $store.state.all"
		>
			<div
				class="card"
				:class="{ active: note == selected_note }"
				@click="select(note)"
			>
				<div class="active-note-line">
					<span class="note-text">
						<input
							class="edit-note-input"
							v-if="note == selected_note"
							v-model="note.I"
							v-on:keyup.enter="editNote(note)"
						/>
						<span v-else>{{ note.I }}</span>
					</span>
					<span class="note-buttons" v-if="note == selected_note">
						<button class="add-button" @click="toggleAddingLink()">
							+
						</button>
					</span>
				</div>
				<div
					class="active-note-links"
					v-if="note == selected_note && add_link_to_selected_note"
				>
					<div class="links-list" v-for="link in note.O">
						<li>{{ getDingByLink(link).I }}</li>
					</div>
					<input
						class="new-link-input"
						v-model="new_link"
						v-on:keyup.enter="AddLinkToSelectedNote()"
					/>
				</div>
			</div>
		</div>
</template>

<script>
export default {
    data() {
		return {
			selected_note: null,
			add_link_to_selected_note: false,
			new_link: "",
		};
	},
    methods: {
		getDingByLink(link) {
            let id = Object.values(link)[0];
			let ding = this.$store.state.all.filter((x) => x._id == id)[0] ?? {
				I: "! " + id,
			};
			if (ding.I[0] == "!") {
				console.log("ding not found");
				return false;
			}
			return ding;
		},
		select(note) {
			if (this.selected_note != note) {
				this.selected_note = note;
				this.add_link_to_selected_note = false;
			}
		},
		deselect() {
			this.selected_note = null;
		},
		editNote(note) {
			this.$store.dispatch("updateNote", {
				note: note,
				callback: (note) => {},
			});
			this.deselect();
		},
		toggleAddingLink() {
			this.add_link_to_selected_note = !this.add_link_to_selected_note;
		},
		AddLinkToSelectedNote() {
			if (this.selected_note && this.new_link.length > 0) {
				this.$store.dispatch("addLinkToNote", {
					note: this.selected_note,
					link: this.new_link,
					callback: (note) => {
						this.new_link = "";
					},
				});
			}
		},
	},
    mounted(){
        this.$store.dispatch("init")
    }
}
</script>

<style>
.card {
	border: solid;
	border-width: 1px;
	border-radius: 0.5em;
	border-color: white;
	margin: 0.5em;
	padding: 0.5em;

	text-align: left;
}

.active {
	border-color: black;
}

.active-note-line {
	display: flex;
	justify-content: space-between;
}


.edit-note-input {
	font: inherit;
	font-size: inherit;
	color: inherit;

	width: calc(100vw - 4em);

	box-sizing: border-box;
	outline: none;
	border: none;
}

.add-button {
	border: none;
	font-size: 1.4em;
	font-weight: bold;
	color: #28a745;
	background-color: inherit;
	padding: 0;
	margin-right: 0.2em;
	margin-top: -0.1em;
	margin-bottom: -0.1em;
}

.links-list {
	margin-left: 1em;
}

</style>