import { createStore } from "vuex";
import * as api from "./api";
import auth0 from "auth0-js";
import router from "./router";

export default createStore({
	state: {
        // auth
        auth0: new auth0.WebAuth({
			domain: process.env.VUE_APP_AUTH0_CONFIG_DOMAIN,
			clientID: process.env.VUE_APP_AUTH0_CONFIG_CLIENTID,
			redirectUri: process.env.VUE_APP_DOMAINURL + "/auth0callback",
			responseType: process.env.VUE_APP_AUTH0_CONFIG_RESPONSETYPE,
			scope: process.env.VUE_APP_AUTH0_CONFIG_SCOPE,
			audience: process.env.VUE_APP_AUTH0_CONFIG_API_IDENTIFIER,
		}),
        userIsAuthenticated: false,
        userEmail:null,
        userData:null,
        userToBeCreated:false,
        userNameAvailable:true,

        // dinges
        all: [],
	},
	mutations: {
        // auth
        // used in router
        setUserIsAuthenticated(state, replacement) {
			state.userIsAuthenticated = replacement;
		},
        getAllFromUserData(state){
            api.getMultiple(state.userData.O,(res)=>{
                state.all = res;
            })
        },
        // app

        createNote(state, {note, callback}){
            // note as string
            let new_object = {
				I: note,
				O: [],
			};
			api.createDing(new_object, (new_ding) => {
				state.all.push(new_ding);

                state.userData.O.push(new_ding._id);
                api.updateUser(state.userData,()=>{});

				callback(new_ding);
			});
        },
        
	},
	actions: {
        // auth
        auth0Login(context) {
			context.state.auth0.authorize();
		},
		auth0HandleAuthentication(context) {
			context.state.auth0.parseHash((err, authResult) => {
				if (
					authResult &&
					authResult.accessToken &&
					authResult.idToken
				) {
					let expiresAt = JSON.stringify(
						new Date().getTime() + authResult.expiresIn * 1000
					);
					localStorage.setItem(
						"access_token",
						authResult.accessToken
					);
					localStorage.setItem("id_token", authResult.idToken);
					localStorage.setItem("expires_at", expiresAt);
					context.dispatch("auth0getUserEmail");
					router.replace("/");
					if (err) {
						alert("login failed");
						router.replace("/");
						console.log(err);
					}
				}
			});
		},
		auth0getUserEmail(context) {
            if(localStorage.getItem("access_token")){
                context.state.auth0.client.userInfo(
                    localStorage.getItem("access_token"),
                    function (err, user) {
                        if(user){
                            context.state.userEmail = user.email;
                        }     
                    }
                );
            }
		},
		auth0Logout(context) {
			localStorage.removeItem("access_token");
			localStorage.removeItem("id_token");
			localStorage.removeItem("expires_at");

			window.location.href =
				process.env.VUE_APP_AUTH0_CONFIG_DOMAINURL +
				"/v2/logout?returnTo=" +
				process.env.VUE_APP_DOMAINURL +
				"&client_id=" +
				process.env.VUE_APP_AUTH0_CONFIG_CLIENTID;
		},
        checkUserNameAvailabilityAndTrySave(context,name){
            api.checkUserNameAvailability(name,(available) => {
                context.state.userNameAvailable = available;
                if (available) {
                    api.registerUser(context.state.userEmail, name, ()=>{
                        context.state.userToBeCreated = false;
                        context.dispatch("init");
                    });
                }
			})
        },

        // app
        init(context) {
            if(localStorage.getItem("access_token")){
                api.getUserData(context.state.userEmail,(res)=>{
                    if(!res){
                        context.state.userToBeCreated=true;
                        console.log('creating user')
                    }else{
                        context.state.userData = res;
                        context.commit('getAllFromUserData')
                    }
                });
            }
		},
        updateNote(context, {note, callback}){
			api.updateDing(note, (ding) => {
                context.dispatch("init",()=>{});
				callback(ding);
			});
        },
        getOrCreateDingByValue(context,{val,callback}){
            let existing_ding = context.state.all.find((x) => x.I.toLowerCase().replace("?","") == val.toLowerCase().replace("?",""));

            if(context.state.all.length==0){
                console.log("no state")
                callback(null);
            }
			else if (existing_ding) {
				callback(existing_ding);
			} else {
				// in the case that a id is sent
				let existing_ding_by_id = context.state.all.find(
					(x) => x._id == val
				);
				if (existing_ding_by_id) {
					callback(existing_ding_by_id);
				} else {
					context.commit("createNote", {
						note: val,
						callback: (new_ding) => {
							callback(new_ding);
						},
					});
				}
			}
        },
        addLinkToNote(context,{note,link,callback}){
            // link is string
            context.dispatch("getOrCreateDingByValue",{
                val: link,
                callback:(ding)=>{
                    let new_link = {};
                    new_link['link'] = ding._id;;
                    note.O.push(new_link);
                    context.dispatch("updateNote",{
                        note:note,
                        callback:(ding)=>{callback()}
                    });
                }
            })
        }
    },
	modules: {},
});
